import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from '../environments/environment';
import { Employee } from './model/employee';

@Injectable({
  providedIn: 'root'
})
export class HttpService {

  //baseUrlEmp:string="http://localhost:8080/employee/";
  //baseUrlCountry:string="http://localhost:8080/country/";
  baseUrlEmp: string= environment.baseUrlEmp;
  baseUrlCountry: string= environment.baseUrlCountry;

  constructor(private http:HttpClient) { }

  login(obj){
    return (this.http.post(`${this.baseUrlEmp}login`,obj));
  }

  getAllEmployeeData(){
    return (this.http.get<Employee[]>(`${this.baseUrlEmp}getAllEmployee`));
  }

  getEmployeeDataById(id){
    return (this.http.get(`${this.baseUrlEmp}getEmployeeById?empid=${id}`))
  }

  getAllCountry() {
    return (this.http.get(`${this.baseUrlCountry}getAllCountry`));
  }

  addEmployee(obj) {
    return (this.http.post(`${this.baseUrlEmp}addEmployee`,obj,{responseType:'text'}));
  }

  updateEmployee(obj){
    return (this.http.put(`${this.baseUrlEmp}updateEmployee`,obj,{responseType:'text'}));
  }

  deleteEmployee(empid: number) {
    return (this.http.delete(`${this.baseUrlEmp}deleteEmployeeById?empid=${empid}`,{responseType:'text'}));
  }

  changeStatus(empid: number) {
    return (this.http.get(`${this.baseUrlEmp}changeStatus/${empid}`,{responseType:'text'}));
  }
}
