import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';


const routes: Routes = [
  // in case of authentication, angular checks pathmatch
  // or when u ll be in child component, to redirect to login, use this full path
  {
    path:"",redirectTo:"/login",pathMatch:"full"
  },
  {
    path:"login",component:LoginComponent
  },
  {
    path:"dashboard",loadChildren:()=>import('./moduleForLazyLoading/module-for-lazy-loading.module')
    .then(m=>m.ModuleForLazyLoadingModule)
  },
  {
    path:"**",pathMatch:'full', redirectTo:'/login'
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
